import React, { Component } from "react";
import "./styles.css";

let ti = 0;
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      page: 1,
      data: []
    };
  }
  componentDidMount() {
    ti = setInterval(() => {
      console.log("timer: ", this.state.page);
      if (this.state.page < 10) {
        this.setState(
          {
            page: this.state.page + 1
          },
          () => {
            this.handleFormSubmit(this.state.page);
          }
        );
      }
      if (this.state.page == 10) {
        clearInterval(ti);
      }
    }, 1000);
  }

  componentWillUnmount() {
    clearInterval(ti);
  }

  handleFormSubmit = (item) => {
    console.log("Item", item);
    const url =
      "https://api.aladhan.com/timingsByCity?city=" +
      item +
      "&country=US&method=2";
    fetch(url)
      .then((response) => {
        return response.json();
      })
      .then((json) => {
        console.log("data", json);
        this.setState({
          data: [...this.state.data, json.data]
        });
      });
  };
  render() {
    console.log("statedata: ", this.state.data);
    return (
      <div>
        <div
          style={{
            display: "flex",
            flex: 1,
            alignItems: "center",
            border: "1px solid black"
          }}
        >
          <div style={{ flex: 0.5 }}>Name</div>
          <div style={{ flex: 0.5 }}>Page</div>
        </div>
        {this.state.data.map((item, key) => (
          <div
            key={key}
            style={{
              display: "flex",
              flex: 1,
              alignItems: "center",
              border: "1px solid black"
            }}
          >
            <div style={{ flex: 0.5 }}>{item.timings.Fajr}</div>
            <div style={{ flex: 0.5 }}>{item.timings.Fajr}</div>
          </div>
        ))}
      </div>
    );
  }
}

export default App;
